<template>
    <view class="nvue-page-root">
      <view class="uni-common-mt">
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>车主姓名</text></view>
          <view class="uni-input-wrapper">
            <input class="uni-input" type="text" v-model="params.name" disabled="disabled" placeholder="请输入" />
          </view>
        </view>
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>联系电话</text></view>
          <view class="uni-input-wrapper">
              <input class="uni-input" v-model="params.phone" type="number" placeholder="请输入" />
          </view>
        </view>
        <view class="uni-form-item uni-column">
          <view class="title"><text class="uni-form-item__title"><text>*</text>紧急联系电话</text></view>
          <view class="uni-input-wrapper">
              <input class="uni-input" v-model="params.emergencyPhone" type="number" placeholder="请输入" />
          </view>
        </view>
      </view>
      <view class="submit">
        <button @click="saveInfo"> 确 定 </button>
      </view>
    </view>
</template>
<script>
  export default {
    data() {
      return {
        params: {
          name: null,
          phone: null,
          emergencyPhone: null,
          createUserId: null
        }
      }
    },
    onLoad(options){
      let userInfo = JSON.parse(options.params)
      console.log(userInfo)
      this.params.emergencyPhone = userInfo.emergencyPhone
      this.params.name = userInfo.name
      this.params.phone = userInfo.phone
      this.params.createUserId = userInfo.createUserId
    },
    methods: {
      saveInfo(){
        let data = this.params
        if(data.name == null){
          uni.showToast({
            title:"姓名不能为空" ,
            icon:'none',
	          duration: 2000
          })
        }else if(data.phone == null){
          uni.showToast({
            title:"联系电话不能为空" ,
            icon:'none',
            duration: 2000
          })
        }else if(data.emergencyPhone == null){
          uni.showToast({
            title:"紧急联系电话不能为空" ,
            icon:'none',
            duration: 2000
          })
        }else{
          this.$http.updateCarInfo(data).then(res=>{
            uni.switchTab({
              url:'/pages/user/mine'
            })
          })
        }
      }
    },
  }
</script>

